
import React from 'react';
import SearchBar from './SearchBar'
import ImageList from './ImageList'
import unsplash from '../api/unsplash'
import './style.css'

class App extends React.Component{
    state={images : []};
  Sub = async (term) => {
        const response = await unsplash.get(('/search/photos'),{
            params:{query:term}
        }
         
        );

        
        this.setState({images: response.data.results });

    };



    render(){
    return(
        <div className="bodyy">
            <center>
           <a href="https://www.instagram.com/iamzeo001/"><img src="https://i.ibb.co/p4sRnWq/shah.png" className='logo' alt="shah" border="0"/></a>
            <div> <h2>This App is made by Anzar Shah as a practice ReactJs Project(Image Search)</h2></div>

            
            <div className="contact"> <h4> MY WEBSITE <a href="https://www.anzarshah.online/"> CLICK HERE </a> </h4> </div>
            </center>
        <div className="ui container" style={{marginTop:'10px'}}>
            <SearchBar onSubmit={this.Sub} />
            Found {this.state.images.length} images..
            <center>
            <ImageList images={this.state.images}/>
            </center>
        </div>
        </div>
          );
         }
        }


export default App;